import { MessageBox, Message } from 'element-ui'

/**
 * 
 * */
const Tools = {
  // 
  successTips (message, close = () => {}) {
    Message.closeAll()
    Message({ message, type: 'success', duration: 1500, onClose: close })
  },

  // 
  warningTips (message) {
    Message.closeAll()
    Message({ type: 'warning', message, duration: 3000 })
  },

  // 
  errorTips (message) {
    Message.closeAll()
    Message({ type: 'error', message, duration: 3000 })
  },

  // 
  confirmBox ({ message, title = '', confirm = () => {}, cancel = () => {} }) {
    MessageBox.confirm(message, title, {
      confirmButtonText: '',
      cancelButtonText: '',
      type: 'warning'
    }).then(confirm).catch(cancel)
  },

  // 
  alertBox (message, callback = () => {}) {
    MessageBox.alert(message, '', {
      confirmButtonText: ''
    }).then(callback)
  },

  // 
  confirmDelete (confirm, cancel) {
    this.confirmBox({ message: '', confirm, cancel })
  }
}

export default Tools
